import type { AuthenticationData, LoginAuthenticationData, UpdateAuthenticationData } from '@strife/common';
import { ServiceIdentifier } from 'inversify';
import type { IUser } from '../user.interface';
import { IBaseService } from './base.service.interface';

export interface IAuthenticationService extends IBaseService {
  user?: IUser;
  login(data: LoginAuthenticationData): Promise<AuthenticationData>;
  logout(): Promise<void>;
  updateAuthentication(data: UpdateAuthenticationData): Promise<AuthenticationData>;
}

export const AuthenticationServiceServiceId: ServiceIdentifier<IAuthenticationService> = Symbol.for('AuthenticationServiceServiceId');