'use client'

import Alert from '@mui/material/Alert';
import Button from '@mui/material/Button';
import Link from '@mui/material/Link';
import Paper from '@mui/material/Paper';
import TextField from '@mui/material/TextField';
import Typography from '@mui/material/Typography';
import { ChangeEvent, Fragment, useState } from 'react';
import '../../src/services/account.service';
import '../../src/services/authentication.service';
import { AuthenticationServiceServiceId } from '../../src/services/authentication.service.interface';
import di from '../di';

export default function Home() {

  const [ accessToken, setAccessToken ] = useState('');
  const [ refreshToken, setRefreshToken ] = useState('');

  const [ email, setEmail ] = useState('');
  function onChangeEmail(event: ChangeEvent<HTMLInputElement>): void {
    setEmail(event.target.value)
  }

  const [ password, setPassword ] = useState('');
  function onChangePassword(event: ChangeEvent<HTMLInputElement>): void {
    setPassword(event.target.value);
  }

  const [ error, setError ] = useState(undefined);

  async function onSubmitLogin(event: React.FormEvent<HTMLFormElement>): Promise<void> {
    event.preventDefault();

    const authenticationService = await di.getAsync(AuthenticationServiceServiceId);
    const authenticationData = await authenticationService.login({ email, password });
    
    // const loginResponse = await fetch(
    //   'http://localhost:3000/authentication', 
    //   {
    //     method: 'POST',
    //     headers: { 'Content-Type': 'application/json' },
    //     body: JSON.stringify({
    //       email,
    //       password
    //     })
    //   }
    // );

  }

  return (
    <div className="font-sans grid grid-rows-[20px_1fr_20px] items-center justify-items-center min-h-screen p-8 pb-20 gap-16 sm:p-20">
      <main className="row-start-2 items-center sm:items-start w-md">
        <Paper elevation={4}>
          <form className="flex flex-col gap-[32px] p-8" onSubmit={onSubmitLogin}>
            <Typography variant="h2" align="center" fontWeight={700}>strife</Typography>
            <div className="flex flex-col gap-[8px]">
              {
                error == undefined
                  ? <Fragment />
                  : <Alert variant="filled" severity="error">{error}</Alert>
              }
              <TextField name="email" type="email" value={email} onChange={onChangeEmail} placeholder="email" />
              <TextField name="password" type="password" onChange={onChangePassword} placeholder="password" />
              <Button type="submit">Login</Button>
              <Link href="/register" className="text-center" fontSize={'small'}>Don't have an account yet?</Link>
            </div>
          </form>
        </Paper>
      </main>
    </div>
  );
}
